import http from '@/utils/http'

export function getMessage(params, loading = false, loadingText = "..."){
/*
* 
* @param {*} id  id
* 
*/
   const url = `/paper/getMessage`;
   var data = {}
   params = Object.assign(data, params)
   return http.post( url,{
		...params
   },{
	   loading,
	   loadingText
   })
}