	connectMiniPro(Vue){
		let socket = ioMiniPro(`${configs.wsUrl}`)
		// socket  Vue
		Vue.prototype.$socket = socket		
        // 
        socket.on("connectionSuccess",function(data){
            console.log("app onLaunch  ----------------socket")
            //  store 
            store.dispatch("setSocketStatus", true)
            //  bindId 
            socket.emit("bindId",{
                // userId: store.getters.userId
                userId: '57',
                token: 'oh_g55CR__hDw53k1WHjDfoCGZh0'
            })
            console.log(" bindId", JSON.stringify({
                userId: store.getters.userId,
                // token: 'oh_g55CR__hDw53k1WHjDfoCGZh0'
				token: store.getters.userToken
            }))
        })	
        
		//  socket 
        socket.on("emitFixedCustomerInfo", function(data){
            console.log("")
            // getDeviceApi().showToast({
            //     title: JSON.stringify(data)
            // })
            getDeviceApi().showModal({
				 title: 'socket',
				 content: `${data.from_id} :${data.from_msg}}`,
				 showCancel: true,//
				 cancelText:"",//
				 cancelColor:'skyblue',//
				 confirmText:"",//
				 confirmColor: 'skyblue',//
				 success: function (res) {
					if (res.cancel) {
					   //,
					   
					} else {
					    //
					    let toUrlType = 'navigateTo'
					    if(data.to_url_type){
						   toUrlType = data.to_url_type 
						}
						debugger
						switch (toUrlType){
							case 'navigateTo':
								getDeviceApi().navigateTo({
									url: data.to_url
								})
								break;
							case 'switchTab':
								getDeviceApi().switchTab({
									url: data.to_url
								})
								break;
							case 'reLaunch':
								getDeviceApi().reLaunch({
									url: data.to_url
								})
								break;	
							case 'redirectTo':
								getDeviceApi().redirectTo({
									url: data.to_url
								})
								break;																
							default:
								getDeviceApi().switchTab({
									url: '/pages/find/find'
								})							
								break;
						}
					}
				 },
				 fail: function (res) { },//
				 complete: function (res) { },//
			})			
        })  
    },	

	//              
	getMessage(userId){
		return new Promise((resolve, reject) => {
			getMessage(userId).then(res => {
				// debugger
				console.log(res)
				if(res.statusCode == 200){
					resolve(res.data)
				}else {
					reject("")
				} 
			}).catch(() => {
				
			})	
		})	
	},
